<template>
    <div class="v-container">
        <mt-header title="">
            <router-link to="/userinfo?role=0" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="info-part">
            <mt-cell title="" value=""></mt-cell>
            <mt-field label="" type="textarea" rows="4" readonly v-model="backInfo"></mt-field>
        </div>
        <div class="info-part">
            <mt-button plain @click="clickGoBack" size="large">&nbsp;</mt-button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'InviteDetail',
    data() {
        return {
            title: '',
            backInfo: '.....'
        }
    },
    methods: {
        clickGoBack() {
            this.$router.push('/userinfo?role=0')
        }
    }
}
</script>
<style lang="less" scoped>
.info-part {
    margin-top: 30px;
    padding: 0 10px;
    button {
        margin-top: 10px;
    }
}
</style>



